<template>
<!-- <nav-component></nav-component> -->
<nav class="navbar fixed-top navbar-expand-lg navbar-light bg-white">
    <div class="container-fluid">
      <div class="logo">
        <router-link class="text-decoration-none" to="/"
          >Cariinkerja</router-link
        >
      </div>
      <!-- <a class="navbar-brand" href="#" style="font-weight: 700; color: blue;">CariinKerja</a> -->
      <button
        class="navbar-toggler"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav me-auto mb-2 mb-lg-0">
          <router-link class="nav-link" to="/">Home</router-link>
          <!-- <a class="nav-link active" aria-current="page" href="index.html">Home</a> -->

          <router-link class="nav-link" to="/postjob">Postjob</router-link>
          <!-- <a class="nav-link" href="#">Postjob</a> -->

          <router-link class="nav-link" to="/about">About</router-link>
          <!-- <a class="nav-link">About</a> -->
          
        </ul>
        <form class="d-flex">
          <p>Welcome back, {{ user }}</p>
          <!-- <a class="btn btn-primary" href="login.html" role="button">Login</a> -->
        </form>
      </div>
    </div>
  </nav>



  <div class="container">
    <button
      type="button"
      class="btn btn-success"
      data-bs-toggle="modal"
      data-bs-target="#exampleModal"
      data-bs-whatever="@getbootstrap"
    >
      Add New Job
    </button>

    <div
      class="modal fade"
      id="exampleModal"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel1">New message</h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body">
            <form>
              <div class="mb-3">
                <label for="recipient-name" class="col-form-label"
                  >Job Name:</label
                >
                <input type="text" class="form-control" id="recipient-name" v-model="jobName" />
              </div>
              <div class="mb-3">
                <label for="recipient-name" class="col-form-label"
                  >Job Salary:</label
                >
                <input type="number" class="form-control" id="recipient-name" v-model="jobSalary" />
              </div>
              <div class="mb-3">
                 <label for="inputState">Job Position</label>
                    <select class="form-control" id="inputState" v-model="jobPosition">
                      <option selected>Choose..</option>
                      <option>Internship</option>
                      <option>Full time</option>
                      <option>Part Time</option>
                      <option>Contractual</option>
                      <option>Freelance</option>
                    </select>
              </div>
              <div class="mb-3">
                <label for="recipient-name" class="col-form-label"
                  >Job Requirement:</label
                >
                <input type="text" class="form-control" id="recipient-name" v-model="jobRequirement" />
              </div>
              <div class="mb-3">
                <label for="message-text" class="col-form-label"
                  >Job Desc:</label
                >
                <textarea class="form-control" id="message-text" v-model="jobDesc"></textarea>
              </div>
              <div class="mb-3">
                <label for="message-text" class="col-form-label"
                  >Job Address:</label
                >
                <textarea class="form-control" id="message-text" v-model="jobAddress"></textarea>
              </div>
            </form>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-danger"
              data-bs-dismiss="modal"
            >
              X
            </button>
            <button v-on:click="addjob" type="button" class="btn btn-primary" >Add</button>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
  <div class="col mt-3" v-for="item in list" v-bind:key="item.id">
   <job-component :item="item"></job-component>
  </div>
  <button v-on:click="logout" class="btn btn-danger">Logout</button>
   
   
  </div>
</div>
  
 
</template>

<script>
// import navbar from '../components/NavComponent.vue';
import axios from "axios";
import { createToast } from "mosha-vue-toastify";
import JobComponent from "../components/JobComponent.vue";


export default {
  
  name: "DashboardView",
  components: {
    // NavComponent : navbar,
    JobComponent : JobComponent 
  },
  
  data() {
    return {
      jobName:"",
      jobSalary:"",
      jobPosition:"",
      jobRequirement:"",
      jobDesc:"",
      jobAddress:"",
      list:[],
      
      
    };
  },
  methods : {
    // func tambah job
    async addjob() {
      try {
        const result = await axios.post(
          `http://54.255.4.75:9091/api/v1/job/create?jobName=${this.jobName}&recruiterId=4&jobSalary=${this.jobSalary}&jobPosition=${this.jobPosition}&jobAddress=${this.jobAddress}&jobDesc=${this.jobDesc} products to vendor&jobRequirement=${this.jobRequirement}`
        );
        localStorage.setItem("user-info", JSON.stringify(result.data));
        createToast("Job Successfully Created", { type: "success" });
        
      } catch (error) {
        createToast("please, fill blank form", { type: "danger" });
        console.log(error);
      }
    },
        logout(){
        try {
        localStorage.removeItem("user-info")
        this.$router.push("/login");
        createToast("logout success!", { type: "success" });
        }
        catch (error) {
          console.log(error);
        }
    
    }
  },
  
  // func tampilin data
  mounted(){
    const recruiterId = JSON.parse(localStorage.getItem("user-info")).data.recruiterId

    console.log(recruiterId)
    axios.get(`http://54.255.4.75:9091/api/v1/jobs/${recruiterId}`)
    .then((resp)=>{
      this.list=resp.data
      
      const user = JSON.parse(localStorage.getItem("user-info"))
      this.user = user.data.recruiterCompany
      console.log(user)
    })
  }
  
  
  
  
}; 
</script>  

<style scoped>

.container {
  height: 100vh;
  margin-top: 70px;
  
}

/* =============reusable css============== */
