  align-items: center;
}

.sidebar li {
  height: 50px;
  margin-top: 10px;
  list-style: none;
  display: flex;
  align-items: center;
}

.sidebar li .icon{
  min-width: 60px;
}

.sidebar li a {
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
}

.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 250px;
  padding: 10px 14px;
  background: var(--sidebar-color);
}
.sidebar .image-text img{
  width: 40px;
}
.sidebar header .image-text{
  display: flex;
  align-items: center;
}

.sidebar header {
  position: relative;
}

.sidebar header .toogle{
  background: red;
  position: absolute;
  top: 50%;
  right: -25px;
  width: 25px;
  height: 25px;
  background: var(--primary-color);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 70%;
  color: var(--sidebar-color);
  
}


header .image-text .header-text{
  display: flex;
  flex-direction: column;
}

.header-text .name {
  font-weight: 600;
}


.list-job{
  margin-top: 60px;
}
.row {
  margin:0px;
}
</style>